import { axios } from '../api/axios'
import { getToken } from '../utils/persistToken';

export const CreateOrChangeReview = async (productId, value) => {
  try{
    const config = {
      headers: {
        'Authorization': `Bearer ${getToken()}`,
      },
    };
    const { data } = await axios.post(`/review-create/`, {productId, value}, config);
    return { data };
  } catch(err) {
    return { err };
  }
};
