import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Grid,
  TextField,
  Paper,
  Button,
  IconButton,
  Typography,
} from '@mui/material';
import {
  KeyboardArrowLeft,
  KeyboardArrowRight,
  CancelOutlined,
} from '@mui/icons-material';
import styled, { keyframes } from 'styled-components';

import Carousel from 'nuka-carousel';

import { useUpdateProduct } from '../../hooks/updateProduct';

import { ChangeProduct } from '../../actions/ChangeProduct';

const Main = styled.main`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  background-image: linear-gradient(180deg, #efc8c8 0%, #f9ffff 100%);
`;

const CustomPaper = styled(Paper)`
  box-sizing: border-box;
  display: inline-block;
  margin: 6rem auto;
  padding: 1rem;
`;

const breathe = keyframes`
  0% {
    background-color: #DDDDDD;
    scale: 0.97;
  }
  50% {
    background-color: #FFFFFF;
    scale: 1;
  }
  100% {
    background-color: #DDDDDD;
    scale: 0.97;
  }
`;

const HidrationPaper = styled(Paper)`
  box-sizing: border-box;
  display: inline-block;
  margin: 6rem auto;
  padding: 1rem;
  animation: ${breathe} 3s infinite;
  & > * {
    opacity: 0;
  }
`;

const ImgContainer = styled.div`
  display: inline-block;
  width: 100%;
  height: clamp(200px, 30vw, 300px);
  padding: 6px;
`;

const Img = styled.img`
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  object-fit: contain;
  background-color: #eeeeee;
`;

const CarouselButton = styled(IconButton)`
  margin: 0.5rem !important;
`;

const Item = styled.div`
  position: relative;
  text-align: center;
  font-size: 2rem;
  line-height: 120px;
  height: 100px;
  margin-top: 0.5rem;
  border-radius: 10px;
  background-color: #eeeeee;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  @media (max-width: 768px) {
    height: 60px;
  }
`;

const CloseIcon = styled(CancelOutlined)`
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  &:hover {
    color: white !important;
  }
`;

const UploadButton = styled(Button)`
  margin: 0 auto;
`;

const CarouselCard = ({ url, onClick, onRemove }) => {
  return (
    <Item onClick={onClick} style={{ backgroundImage: `url(${url})` }}>
      <CloseIcon color='darkrose' onClick={onRemove} />
    </Item>
  );
};

const placeholder =
  'https://upload.wikimedia.org/wikipedia/commons/thumb/3/3f/Placeholder_view_vector.svg/681px-Placeholder_view_vector.svg.png';

const UpdateProductComp = () => {
  const { fetched, fetching, article } = useUpdateProduct();
  const [src] = article?.media || [placeholder];

  const [
    { name, price, tags, description, images, imageURLs, imgIdx },
    setState,
  ] = useState({
    name: '',
    price: '',
    tags: '',
    description: '',
    images: [],
    imageURLs: [],
    imgIdx: 0,
  });

  const navigate = useNavigate();

  const handleChange = (key, val) =>
    setState((prev) => ({ ...prev, [key]: val }));

  const setImgIdx = (idx) => handleChange('imgIdx', idx);

  const updateProduct = async () => {
    if (!name || !price || !description || !images.length) {
      alert(
        'Llena los campos obligatorios: Nombre, Precio, Descripcion, Imagenes'
      );
      return;
    }

    const { err } = await ChangeProduct(
      article._id,
      name,
      price,
      tags,
      description,
      images
    );
    if (err) {
      console.error(err);
      return;
    }

    navigate(`/account`);
  };

  useEffect(() => {
    if (fetched && !fetching && article) {
      console.log(article);
      setState((prev) => ({
        ...prev,
        name: article.name,
        price: article.price,
        tags: article.tags.join(' '),
        description: article.description,
        images: article.media.map((image) => image.split('/').pop()),
        imageURLS: article.media.map(
          (image) => `${process.env.REACT_APP_API}${image}`
        ),
        imgIdx: 0,
      }));
    }
  }, [fetched, fetching, article]);

  if (!fetched)
    return (
      <Main>
        <HidrationPaper maxWidth='sm'>
          <Typography
            variant='h4'
            variantMapping={{ h3: 'h1' }}
            textTransform='capitalize'
            width='100%'
            marginBottom={2}
          >
            Agregar Articulo
          </Typography>
          <Grid container maxWidth='sm' spacing={2}>
            <Grid container item xs={12} minHeight={200} flexDirection='column'>
              <ImgContainer>
                <Img
                  src={images.length !== 0 ? imageURLs[imgIdx] : placeholder}
                />
              </ImgContainer>
              <UploadButton color='rose' variant='contained' component='label'>
                Upload File
                <input
                  type='file'
                  hidden
                  multiple
                  accept='image/*'
                  onChange={(e) => {
                    const newImages = [...images, ...e.target.files];
                    const newImageURLs = newImages.map((image) =>
                      URL.createObjectURL(image)
                    );
                    handleChange('images', newImages);
                    handleChange('imageURLs', newImageURLs);
                  }}
                />
              </UploadButton>
              {images.length !== 0 && (
                <Carousel
                  slidesToShow={4}
                  cellSpacing={12}
                  renderCenterLeftControls={({
                    previousDisabled,
                    previousSlide,
                  }) => (
                    <CarouselButton
                      onClick={previousSlide}
                      disabled={previousDisabled}
                    >
                      <KeyboardArrowLeft />
                    </CarouselButton>
                  )}
                  renderCenterRightControls={({ nextDisabled, nextSlide }) => (
                    <CarouselButton onClick={nextSlide} disabled={nextDisabled}>
                      <KeyboardArrowRight />
                    </CarouselButton>
                  )}
                >
                  {images.map((media, idx) => (
                    <CarouselCard
                      key={idx}
                      url={imageURLs[idx]}
                      onClick={() => setImgIdx(idx)}
                      onRemove={() => {
                        handleChange(
                          'images',
                          images.filter((_, i) => i !== idx)
                        );
                        handleChange(
                          'imageURLs',
                          imageURLs.filter((_, i) => i !== idx)
                        );
                      }}
                    />
                  ))}
                </Carousel>
              )}
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Nombre'
                fullWidth
                InputLabelProps={{ shrink: true }}
                onChange={(e) => handleChange('name', e.target.value)}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label='Precio'
                type='number'
                fullWidth
                InputLabelProps={{ shrink: true }}
                value={price}
                onChange={(e) => handleChange('price', e.target.value)}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                label='Etiquetas'
                fullWidth
                InputLabelProps={{ shrink: true }}
                onChange={(e) => handleChange('tags', e.target.value)}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Descripcion'
                fullWidth
                InputLabelProps={{ shrink: true }}
                onChange={(e) => handleChange('description', e.target.value)}
                multiline
                rows={4}
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                color='rose'
                variant='contained'
                fullWidth
                onClick={updateProduct}
              >
                Enviar
              </Button>
            </Grid>
          </Grid>
        </HidrationPaper>
      </Main>
    );

  return (
    <Main>
      <CustomPaper maxWidth='sm'>
        <Typography
          variant='h4'
          variantMapping={{ h3: 'h1' }}
          textTransform='capitalize'
          width='100%'
          marginBottom={2}
        >
          Modificar Articulo
        </Typography>
        <Grid container maxWidth='sm' spacing={2}>
          <Grid container item xs={12} minHeight={200} flexDirection='column'>
            <ImgContainer>
              <Img src={`${process.env.REACT_APP_API}${src}` || placeholder} />
            </ImgContainer>
          </Grid>
          <Grid item xs={12}>
            <TextField
              label='Nombre'
              fullWidth
              InputLabelProps={{ shrink: true }}
              onChange={(e) => handleChange('name', e.target.value)}
              value={name}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              label='Precio'
              type='number'
              fullWidth
              InputLabelProps={{ shrink: true }}
              onChange={(e) => handleChange('price', e.target.value)}
              value={price}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              label='Etiquetas'
              fullWidth
              InputLabelProps={{ shrink: true }}
              onChange={(e) => handleChange('tags', e.target.value)}
              value={tags}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              label='Descripcion'
              fullWidth
              InputLabelProps={{ shrink: true }}
              onChange={(e) => handleChange('description', e.target.value)}
              value={description}
              multiline
              rows={4}
            />
          </Grid>
          <Grid item xs={12}>
            <Button
              color='rose'
              variant='contained'
              fullWidth
              onClick={updateProduct}
            >
              Enviar
            </Button>
          </Grid>
        </Grid>
      </CustomPaper>
    </Main>
  );
};

export default UpdateProductComp;
