import React, { useState, useEffect } from 'react';
import {
  Grid,
  Paper,
  IconButton,
  Typography,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  CircularProgress,
} from '@mui/material';
import { Edit, DeleteForever } from '@mui/icons-material';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { useAuth } from '../../hooks/auth';

import { FetchMyProducts } from '../../actions/FetchMyProducts';
import { DeleteProduct } from '../../actions/DeleteProduct';

const Main = styled.main`
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  background-image: linear-gradient(180deg, #efc8c8 0%, #f9ffff 100%);
`;

const CustomPaper = styled(Paper)`
  box-sizing: border-box;
  display: inline-block;
  margin: 2rem auto;
  padding: 1rem;
`;

const ListPaper = styled(Paper)`
  height: 400px;
  overflow-y: auto;
`;

const RelativeTable = styled(Table)`
  position: relative;
`;

const ProgressContainer = styled.div`
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
`;

const Account = () => {
  const { user } = useAuth();

  const [{ fetched, fetching, products }, setState] = useState({
    fetched: false,
    fetching: false,
    products: [],
  });

  const handleChange = (key, val) =>
    setState((prev) => ({ ...prev, [key]: val }));

  const loadProducts = async () => {
    if (fetching) return;
    handleChange('fetching', true);
    const { err, data } = await FetchMyProducts();
    handleChange('fetched', true);
    handleChange('fetching', false);
    if (err) {
      console.error(err);
      return;
    }

    handleChange('products', data);
  };

  const deleteProduct = async (id) => {
    if (fetching) return;
    handleChange('fetching', true);
    const { err } = await DeleteProduct(id);
    handleChange('fetched', true);
    handleChange('fetching', false);
    if (err) {
      console.error(err);
      return;
    }

    handleChange(
      'products',
      products.filter((product) => product._id !== id)
    );
  };

  useEffect(() => {
    loadProducts();
  }, []);

  return (
    <Main>
      <CustomPaper>
        <Typography
          variant='h4'
          variantMapping={{ h4: 'h1' }}
          textTransform='capitalize'
          width='100%'
          marginBottom={2}
        >
          Informacion de Cuenta
        </Typography>
        <Grid container maxWidth='md' spacing={2}>
          {user?.type === 'seller' && (
            <Grid item xs={12}>
              <Typography variant='h6' textTransform='capitalize'>
                mis servicios y articulos
              </Typography>
              <ListPaper>
                <RelativeTable stickyHeader>
                  <TableHead>
                    <TableRow>
                      <TableCell>Name</TableCell>
                      <TableCell>Price</TableCell>
                      <TableCell>Description</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {fetched &&
                      products.map((product) => (
                        <TableRow key={product._id}>
                          <TableCell>{product.name}</TableCell>
                          <TableCell>{product.price}</TableCell>
                          <TableCell>{product.description}</TableCell>
                          <TableCell>
                            <Link to={`/update-article/${product._id}`}>
                              <IconButton color='primary'>
                                <Edit />
                              </IconButton>
                            </Link>
                            <IconButton
                              onClick={() => deleteProduct(product._id)}
                              color='error'
                            >
                              <DeleteForever />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))}
                    {!fetched && (
                      <ProgressContainer>
                        <CircularProgress />
                      </ProgressContainer>
                    )}
                  </TableBody>
                </RelativeTable>
              </ListPaper>
            </Grid>
          )}
          {user?.type === 'buyer' && (
            <Grid item xs={12}>
              <Typography variant='h6' textTransform='capitalize'>
                Tus pedidos
              </Typography>
              <ListPaper>
                <RelativeTable stickyHeader>
                  <TableHead>
                    <TableRow>
                      <TableCell>Name</TableCell>
                      <TableCell>Price</TableCell>
                      <TableCell>Description</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {fetched &&
                      products.map((product) => (
                        <TableRow key={product._id}>
                          <TableCell>{product.name}</TableCell>
                          <TableCell>{product.price}</TableCell>
                          <TableCell>{product.description}</TableCell>
                          <TableCell>
                            <Link to='/update-article'>
                              <IconButton color='primary'>
                                <Edit />
                              </IconButton>
                            </Link>
                            <IconButton
                              onChange={() => deleteProduct(product._id)}
                              color='error'
                            >
                              <DeleteForever />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))}
                    {!fetched && (
                      <ProgressContainer>
                        <CircularProgress />
                      </ProgressContainer>
                    )}
                  </TableBody>
                </RelativeTable>
              </ListPaper>
            </Grid>
          )}
        </Grid>
      </CustomPaper>
    </Main>
  );
};

export default Account;
