import React, { useState } from 'react';
import styled from 'styled-components';

import {
  TextField,
  Form,
  Grid,
  Paper,
  Button,
  CircularProgress,
  Typography,
} from '@mui/material';
import { Star, StarOutline } from '@mui/icons-material';
import { CreateComment } from '../../actions/CreateComment';
import { useEffect } from 'react';
import { FetchMyReview } from '../../actions/FetchMyReview';
import { CreateOrChangeReview } from '../../actions/CreateOrChangeReview';
import { FetchReviewData } from '../../actions/FetchReviewData';
import { FetchComments } from '../../actions/FetchComments';

const CenteredPaper = styled(Paper)`
  margin: 3rem;
  padding: 2rem;
`;

const CommentPaper = styled(Paper)`
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  width: 100%;
`;

const FilledStar = styled(Star)`
  cursor: pointer;
`;

const UnfilledStar = styled(StarOutline)`
  cursor: pointer;
`;

const CommentCard = ({ seller, comment }) => (
  <CommentPaper>
    <Grid key={comment._id} container>
      <Grid item xs={12}>
        <Typography variant='caption' fontSize={14} color='primary'>
          {comment.user.username}
        </Typography>
        {seller === comment.user._id && (
          <Typography variant='caption' fontSize={14} color='secondary'>
            {' '}
            - Vendedor
          </Typography>
        )}
      </Grid>
      <Grid item xs={12}>
        <Typography variant='body2'>{comment.content}</Typography>
      </Grid>
    </Grid>
  </CommentPaper>
);

const UserFeedback = ({ fetched, fetching, article }) => {
  const [{ loading, content, comments, myReview, reviewData }, setState] =
    useState({
      loading: false,
      content: '',
      comments: article?.comments,
      myReview: 0,
      reviewData: null,
    });

  const handleChange = (field, value) =>
    setState((prev) => ({ ...prev, [field]: value }));

  const refetchComments = async () => {
    if (!fetched || fetching || !article || loading) return;

    handleChange('loading', true);
    const { err, data } = await FetchComments(article._id);
    handleChange('loading', false);
    if (err) {
      console.error(err);
      return;
    }

    handleChange('comments', data);
  };

  const createComment = async () => {
    if (!fetched || fetching || !article || loading) return;

    handleChange('loading', true);
    const { err } = await CreateComment(article._id, content);
    handleChange('loading', false);
    if (err) {
      console.error(err);
      return;
    }

    handleChange('content', '');

    // no funciona con uno, polling
    await refetchComments();
    await refetchComments();
  };

  const getMyReview = async () => {
    if (!fetched || fetching || !article || loading) return;

    handleChange('loading', true);
    const { err, data } = await FetchMyReview(article._id);
    if (err) {
      console.error(err);
      handleChange('loading', false);
      return;
    }

    handleChange('myReview', data.value);

    const { err: reviewDataErr, data: reviewData } = await FetchReviewData(
      article._id
    );
    handleChange('loading', false);
    if (reviewDataErr) {
      console.error(reviewDataErr);
      return;
    }

    handleChange('reviewData', reviewData);
  };

  const changeMyReview = (value) => async () => {
    // prevent racing
    if (!fetched || fetching || !article || loading) return;

    handleChange('loading', true);
    const { err, data } = await CreateOrChangeReview(article._id, value);
    handleChange('loading', false);

    if (err) {
      console.error(err);
      return;
    }

    handleChange('myReview', data.value);
    await getMyReview();
  };

  useEffect(() => {
    getMyReview();
  }, [fetched, fetching, article]);

  return (
    <CenteredPaper>
      <Grid container>
        {/* comments */}
        <Grid container item xs={12} md={8} order={{ xs: 1, sm: 0 }}>
          <Grid item xs={12} marginBottom={2}>
            <TextField
              InputLabelProps={{ shrink: true }}
              label='Comentario'
              value={content}
              onChange={(e) => handleChange('content', e.target.value)}
              multiline
              rows={4}
              fullWidth
            />
            <Button
              color='rose'
              variant='contained'
              fullWidth
              disabled={content.length === 0 || loading}
              onClick={() => createComment()}
            >
              {loading ? <CircularProgress size={24} /> : 'Comentar'}
            </Button>
          </Grid>
          {fetched &&
            comments.map((comment) => (
              <CommentCard
                key={comment._id}
                comment={comment}
                seller={article.user}
              />
            ))}
        </Grid>
        {/* reviews */}
        <Grid
          container
          item
          xs={12}
          md={4}
          flexDirection='column'
          justifyContent='flex-start'
          alignItems='center'
          marginBottom={4}
          order={{ xs: 0, sm: 1 }}
        >
          <div>
            {fetched && reviewData && (
              <>
                <Typography variant='h6'>
                  Puntuacion Promedio:{' '}
                  {reviewData.score ? reviewData.score.toFixed(2) : 0}
                </Typography>
                <Typography variant='h6'>
                  Cantidad de Puntuaciones :{' '}
                  {reviewData.count ? Math.trunc(reviewData.count) : 0}
                </Typography>
                <Typography variant='h6' textTransform='capitalize'>
                  Respuesta General: {reviewData.overallResponse || 'No Hay'}
                </Typography>
              </>
            )}
            <Typography variant='body1' marginTop={2}>
              Your Review:
            </Typography>
            <Grid display='inline-flex'>
              {Array.from({ length: myReview }).map((_, i) => (
                <FilledStar
                  color='warning'
                  key={i + 1}
                  onClick={changeMyReview(i + 1)}
                  disabled={loading}
                />
              ))}
              {Array.from({ length: 5 - myReview }).map((_, i) => (
                <UnfilledStar
                  color='warning'
                  key={myReview + i + 1}
                  onClick={changeMyReview(myReview + i + 1)}
                  disabled={loading}
                />
              ))}
            </Grid>
          </div>
        </Grid>
      </Grid>
    </CenteredPaper>
  );
};

export default UserFeedback;
