import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { FetchArticle } from '../actions/FetchArticle';

export const useUpdateProduct = () => {
  const { id } = useParams();

  const [{ fetched, fetching, article }, setState] = useState({
    fetched: false,
    fetching: false,
    article: null,
  });

  const loadArticle = async () => {
    try {
      setState(prev => ({...prev, fetching: true}));
      const { err, data: article } = await FetchArticle(id);
      if (err) {
        console.error(err);
        return;
      }
      
      setState(prev => ({
        ...prev, 
        fetched: true,
        fetching: false,
        article,
      }));
    } catch (err) {
      setState(prev => ({
        ...prev, 
        fetched: true,
        fetching: false,
      }));
    }
  };

  useEffect(()=>{
    loadArticle();
  }, []);


  return {
    fetched,
    fetching,
    article,
    loadArticle,
  }
};
