
import { axios } from '../api/axios'
import { getToken } from '../utils/persistToken';

export const DeleteProduct = async (productId) => {
  try{ 
    const config = {
      headers: {
        'Authorization': `Bearer ${getToken()}`,
      },
    };
    const { data } = await axios.delete(`/product-delete/${productId}`, config);
    return { data };
  } catch(err) {
    return { err };
  }
};

