import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { FetchArticle } from '../actions/FetchArticle';

export const useArticle = () => {

  const { id } = useParams();

  const [{ 
      fetchedArticle, 
      fetchingArticle, 
      error, 
      article, 
      imgIndex 
    }, 
    setState
  ] =
  useState({
    fetchedArticle: false,
    fetchingArticle: false,
    error: null,
    article: null,
    imgIndex: 0,
  });

  const loadArticle = async () => {
    try {
      setState(prev => ({...prev, fetchingArticle: true}));
      const { err, data: article } = await FetchArticle(id);
      if (err) {
        console.error(err);
        return;
      }
      
      setState(prev => ({
        ...prev, 
        fetchedArticle: true,
        fetchingArticle: false,
        article,
      }));
    } catch (err) {
      setState(prev => ({
        ...prev, 
        fetchedArticle: true,
        fetchingArticle: false,
        fetchError: err.message,
      }));
    }
  };

  const setImgIndex = idx => setState(prev => ({...prev, imgIndex: idx}));

  useEffect(() => {
    loadArticle();
  }, [id]);

  return {
    fetchedArticle,
    fetchingArticle,
    error,
    article,
    loadArticle,
    imgIndex,
    setImgIndex
  };
};