import React from 'react';
import { func, string, instanceOf } from 'prop-types';

import { CircularProgress } from '@mui/material';

import SearchField from '../Common/SearchField';
import ProductCard from './ProductCard';

import styled, { keyframes } from 'styled-components';

//#region Styled
const scrollBackgroundAnim = keyframes`
  0% { background-position: 0 0; }
  100% { background-position: -200% 0; }
`;

const Container = styled.div`
  background: repeating-linear-gradient(
    to right,
    #efc8c8 0%,
    #f9ffff 50%,
    #efc8c8 100%
  );
  background-size: 200% auto;
  background-position: 0 100%;
  animation-name: ${scrollBackgroundAnim};
  animation-timing-function: linear;
  animation-duration: 8s;
  animation-iteration-count: infinite;
`;

const Main = styled.main`
  width: 100%;
  max-width: 1500px;
  margin: 0 auto;
  font-family: 'Inter';
  overflow: auto;
`;

const Title = styled.p`
  font-size: 2rem;
`;

const ProductCardContainer = styled.div`
  display: flex;
  flex-flow: row wrap;
  margin: 0 auto;
  margin-bottom: 2rem;
  justify-content: center;
  gap: 1rem;
  @media (max-width: 768px) {
    padding: 1rem;
  }
`;

const LoadingBar = styled.div`
  display: flex;
  width: 90vw;
  height: 80vh;
  margin: 0 auto;
  overflow: hidden;

  justify-content: center;
  align-items: center;
`;

//#endregion

const Browse = ({ fetching, handleMakeSearch, query, products }) => {
  return (
    <Container>
      <Main>
        <SearchField handleMakeSearch={handleMakeSearch} />
        {query && <Title>Results for {query}</Title>}
        {fetching && (
          <LoadingBar>
            <CircularProgress color='primary' />
          </LoadingBar>
        )}
        <ProductCardContainer>
          {!fetching &&
            products.map((product) => (
              <ProductCard key={product._id} product={product} />
            ))}
        </ProductCardContainer>
      </Main>
    </Container>
  );
};

Browse.propTypes = {
  handleMakeSearch: func.isRequired,
  query: string,
  products: instanceOf(Array),
};

Browse.defaultProps = {
  query: '',
  products: [],
};

export default Browse;
