
import { axios } from '../api/axios'
import { getToken } from '../utils/persistToken';

export const ChangeProduct = async (id, name, price, tags, description) => {
  try{ 
    const config = {
      headers: {
        'Authorization': `Bearer ${getToken()}`,
        'Content-Type': 'multipart/form-data'
      },
    };

    const fd = new FormData();
    fd.set('name', name);
    fd.set('price', price);
    fd.set('tags', tags);
    fd.set('description', description);

    await axios.put(`/product-update/${id}`, fd, config);
    return { data: true };
  } catch(err) {
    return { err };
  }
};

